				})
				.catch(err => {
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			//日报营收分析
			incomeStatisticsByDay(){
				const _that = this;
				const data ={
					date:"2022-4-12",
					parkCode:"1014GS60Z1HWZCX",
				}
				_that.$utils.request(_that.$god.operationalAnalysis.incomeStatisticsByDay, data, 'GET')
				.then(res => {	
					this.optionRevenueStatisticsCD = {
					    categories: [],
					    series: [
							{
								data:[]
							}
					    ]
					};
					for(let i=0;i<24;i++){
						let data;
						if(i<10){
							data = "0"+i+"时";
						}
						else{
							data = i+"时";
						}
						this.optionRevenueStatisticsCD.categories.push(data)
					}
					res.data.forEach((item,index)=>{
						this.optionRevenueStatisticsCD.series[0].data.push(item.timeIncome);
					})
					console.log("this.optionRevenueStatisticsCD")
					console.log(this.optionRevenueStatisticsCD)
				})
				.catch(err => {
					console.log(err)
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			//月报营收分析
			incomeStatisticsByMonth(){
				const _that = this;
				const data ={
					yearMonth:"2022-3",
					parkCode:"1014GS60Z1HWZCX",
				}
				_that.$utils.request(_that.$god.operationalAnalysis.incomeStatisticsByMonth, data, 'GET')
				.then(res => {	
					for(let i=0;i<30;i++){
						let data = i + "日";
						this.optionRevenueStatisticsMonth.xAxis.data.push(data)
					}
					//本月
					res.data.monthDataList.forEach((item,index)=>{
						this.optionRevenueStatisticsMonth.series[0].data.push(item.timeIncome);
						// this.statisticsCountShow.out+=item.timeCount;
					})
					//上月
					res.data.lastMonthDataList.forEach((item,index)=>{
						this.optionRevenueStatisticsMonth.series[1].data.push(item.timeIncome);
						// this.statisticsCountShow.in+=item.timeCount;
					})
				})
				.catch(err => {
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			//停车流量-日报
			getStatisticsCountVOByDay(){
				const _that = this;
				const data ={
					date:"2022-4-12",
					parkCode:"1014GS60Z1HWZCX",
					// yearMonth:3,
				}
				_that.$utils.request(_that.$god.operationalAnalysis.statisticsCountVOByDay, data, 'GET')
				.then(res => {	
					for(let i=0;i<24;i++){
						let data;
						if(i<10){
							data = "0"+i+":00";
						}
						else{
							data = i+":00";
						}
						this.optionParkingFlow.xAxis.data.push(data)
					}
					//出流量
					res.data.outLineChartList.forEach((item,index)=>{
						this.optionParkingFlow.series[0].data.push(item.timeCount);
						this.statisticsCountShow.out+=item.timeCount;
					})
					//入流量
					res.data.inLineChartList.forEach((item,index)=>{
						this.optionParkingFlow.series[1].data.push(item.timeCount);
						this.statisticsCountShow.in+=item.timeCount;
					})
				})
				.catch(err => {
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			//停车流量月报
			getStatisticsCountVOByMonth(){
				const _that = this;
				const data ={
					yearMonth:"2022-3",
					parkCode:"1014GS60Z1HWZCX",
					// yearMonth:3,
				}
				_that.$utils.request(_that.$god.operationalAnalysis.statisticsCountVOByMonth, data, 'GET')
				.then(res => {	
					for(let i=0;i<24;i++){
						let data;
						if(i<10){
							data = "0"+i+":00";
						}
						else{
							data = i+":00";
						}
						this.optionParkingFlowMonth.xAxis.data.push(data)
					}
					//出流量
					res.data.outLineChartList.forEach((item,index)=>{
						this.optionParkingFlowMonth.series[0].data.push(item.timeCount);
						this.statisticsCountShow.out+=item.timeCount;
					})
					//入流量
					res.data.inLineChartList.forEach((item,index)=>{
						this.optionParkingFlowMonth.series[1].data.push(item.timeCount);
						this.statisticsCountShow.in+=item.timeCount;
					})
				})
				.catch(err => {
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
