				const _that = this;
				const data ={
					yearMonth:"2022-3",
					parkCode:"1014GS60Z1HWZCX",
				}
				_that.$utils.request(_that.$god.operationalAnalysis.parkTurnoverRateByMonth, data, 'GET')
