						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			//日报利用率
			parkUseRateByDay(){
				const _that = this;
				const data ={
					date:"2022-4-12",
					parkCode:"1014GS60Z1HWZCX",
					// yearMonth:3,
				}
				_that.$utils.request(_that.$god.operationalAnalysis.parkUseRateByDay, data, 'GET')
				.then(res => {	
					this.optionUtilizationRate.series[0].data[0].value= Number(res.data.value);
					this.optionUtilizationRate.series[0].data[1].value= 1-Number(res.data.value);
					this.optionUtilizationRate.series[0].data[2].value= 1;
					this.utilizationRate = res.data.value*10;
					this.$forceUpdate();
				})
				.catch(err => {
					console.log(err)
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			//月报利用率
			parkUseRateByMonth(){
				const _that = this;
				const data ={
					yearMonth:"2022-3",
