					//出流量
					res.data.useList.forEach((item,index)=>{
						this.optionUtilizationRateMonth.series[0].data.push(item.value);
					})
				})
				.catch(err => {
					uni.showModal({
						title: '提示',
						content: err.data.msg,
						showCancel: false
					})
				});
			},
			logstatrt() {
				//默认数据
				//总数据统计分析
				this.incomeStatisticsByDayTop();
				//日报营收分析
				this.incomeStatisticsByDay();
				//日报停车场流量
				this.getStatisticsCountVOByDay();
				//日报周转率 turnoverRate
				this.parkTurnoverRateByDay();
				//日报利用率
				this.parkUseRateByDay();
			},
			//切换至月收入
			switchMonth(){
				//总数据统计分析
				this.incomeStatisticsByMonthTop();
				//月报营收分析
				this.incomeStatisticsByMonth();
				//月报停车场流量
				this.getStatisticsCountVOByMonth();
				//月报周转率 turnoverRate
				this.parkTurnoverRateByMonth();
				//月报利用率
				this.parkUseRateByMonth();
			},
		},
		
	};
